/**
 * Reviewer Plugin v.2
 * Created by Michele Ivani
 */
(function() {

    if (typeof rwpTinyMCEContent !== 'undefined' ) {
        tinymce.PluginManager.add('rwp_reviewer', function( editor, url ) {

            // Prepare button menu
            var reviewerMenu = [];

            if( rwpTinyMCEContent.reviews.length > 0 ) { 
                                
                jQuery.each( rwpTinyMCEContent.reviews, function( i, review ) {

                    reviewerMenu.push({
                        text: review.review_title,
                        value: '[rwp-review id="'+ review.review_id +'"]',
                        onclick: function() {
                            editor.insertContent(this.value());
                        }
                    });
                });
            }

            if( rwpTinyMCEContent.tables.length > 0 ) {

                jQuery.each( rwpTinyMCEContent.tables, function( i, table ) {
                    
                    reviewerMenu.push({
                        text: table.table_title,
                        value: '[rwp-table id="'+ table.table_id +'"]',
                        onclick: function() {
                            editor.insertContent(this.value());
                        }
                    });
                });
            }

            if( rwpTinyMCEContent.reviews.length <= 0  && rwpTinyMCEContent.tables.length <= 0) {
                reviewerMenu.push({
                    text: rwpTinyMCEContent.msg,
                    onclick: function() {}
                });
             }

            // Add button to the editor
            editor.addButton( 'rwp_reviewer', {
                title: 'Reviewer | Reviews and Tables',
                type: 'menubutton', 
                menu: reviewerMenu
            });

        });

        tinymce.PluginManager.add('rwp_reviewer_widget', function( editor, url ) {

            // Add button to the editor
            editor.addButton( 'rwp_reviewer_widget', {
                title: 'Reviewer | Reviews List',
                onclick: function() {
                    var width = jQuery(window).width(), H = jQuery(window).height(), W = ( 720 < width ) ? 720 : width;
                    W = W - 80;
                    H = H - 84;
                    tb_show( 'Reviewer | Reviews List', '#TB_inline?width=' + W + '&height=' + H + '&inlineId=rwp-reviews-list-form' );
                }
            });

        });
    }
})();